<template>
  <div id="addadmin">
    <chathead></chathead>
    <h1 id="title"></h1>
    <div class="input_area">
      <h4>:</h4>
      <el-input placeholder="" v-model="name" class="input"></el-input>
    </div>
    <div class="input_area">
      <h4></h4>
      <el-checkbox-button v-model="adduser"></el-checkbox-button>
      <el-checkbox-button v-model="deluser"></el-checkbox-button>
      <el-checkbox-button v-model="addpower"></el-checkbox-button>
      <el-checkbox-button v-model="delpower"></el-checkbox-button>
    </div>
    <el-button type="warning" @click="addpow"></el-button>
  </div>
</template>
<script>
import chathead from '../common/chat_head';
export default {
  name: "addadmin",
  components:{
    chathead,
  },
  data() {
    return {
      name: "",
      adduser: false,
      deluser: false,
      addpower: false,
      delpower: false
    };
  },
  methods: {
    addpow() {
      let $this = this;
      this.$axios
        .post(`http://111.230.63.143:3005/addpower/addadmin`, {
          username:$this.name,
          adduser: $this.adduser,
          deluser: $this.deluser,
          addadmin: $this.addpower,
          deladmin: $this.delpower
        })
        .then(res => {
          if (res.data == 1) alert("");
          else alert("");
        });
    }
  }
};
</script>

<style lang="less" scoped>
#addadmin {
  height: 100%;
  flex: 1;
  text-align: center;
}
.input {
  width: 40%;
}
.input_area {
  margin: 20px;
}
h4 {
  margin: 5px;
}
</style>




