<template>
  <div id="adduser">
    <chathead></chathead>
    <h1 id="title"></h1>
    <div class="input_area">
      <h4>:</h4>
      <el-input v-model="name" placeholder="" class="input"></el-input>
    </div>
    <div class="input_area">
      <h4>:</h4>
      <el-input v-model="name1" placeholder="" class="input"></el-input>
    </div>
     <el-button type="danger" @click="rmvuser"></el-button>
  </div>
</template>
<script>
import chathead from '../common/chat_head';
export default {
  name:'adduser',
  components:{
    chathead
  },
  data(){
    return{
      name:'',
      name1:'',
      list:[],
    }
  },
  methods:{
    rmvuser(){
      if(this.$common.ifnull(this.name,this.name1))return;
      if(this.name!=this.name1){
        alert("!");
        return;
      }
      this.$axios.post(`http://111.230.63.143:3005/addpower/deluser`,{
        name:this.name,
        executor:localStorage.username,
      }).then((res)=>{
        if(res.data==1){
          alert("!")
        }else{
          alert("!")
        }
        this.name="";
        this.name1="";
      })
    }
  }
}
</script>

<style lang="less" scoped>
#adduser {
  height: 100%;
  flex: 1;
  text-align: center;
}
.input_area{
  margin: 20px;
}
.input{
  width: 40%;
}

h4{
  margin: 5px;
}
</style>




